// colors
$white: #FFFFFF;
$blueCharcoal: #22262A;
$radicalRed: #FF4858;
$spindle: #C1C8CE;

// fonts
// $proxima: 'Proxima Nova Rg'; 
// @font-face {
//     font-family: 'Proxima Nova Rg';
